import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu2 from '../components/Menu2';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import Divider from '../components/Divider';
import SizeableImage from '../components/SizeableImage';
import '../index.css';

import functioned from '../assets/images/food/functioned.png';
import defined from '../assets/images/food/defined.jpg';
import scenario2 from '../assets/images/food/scenario2.jpg';
import choose from '../assets/images/food/choose.png';
import page_architecture from '../assets/images/food/page_architecture.jpg';
import wireframes from '../assets/images/food/wireframes.png';
import food_hero2 from '../assets/images/food/food_hero2.jpg';
import food_hero3 from '../assets/images/food/food_hero3.jpg';
import prototyping from '../assets/images/food/prototyping.jpg';
import journey2 from '../assets/images/food/journey2.png';
import journey3 from '../assets/images/food/journey3.png';
import personas from '../assets/images/food/personas.jpg';
import finalscreens1 from '../assets/images/food/finalscreens1.jpg';
import fontcolor from '../assets/images/food/fontcolor.jpg';
import food_hero5 from '../assets/images/food/food_hero5.jpg';
import admockups from '../assets/images/food/admockups.jpg';
import gif1 from '../assets/images/food/food_gif1.gif'

import journeycompiled from '../assets/images/food/journey_compiled.png';
import food_interview from '../assets/images/food/food_interview.png';
import finalscreens2 from '../assets/images/food/finalscreens2.jpg';

function FoodPage(props) {

  return (
    <div>
    <BackButton2/>
      <div class="sidenav">
      <hr class="contentsdivider"/>
        <Menu2 />
        <hr class="contentsdivider"/>
      </div>
      <div class="sidemain">
        <div id="Summary" className="anchor"/>
        <Container>

        <HeroImage imagesource={food_hero5}/>
        <Spacer />
        <CenteredBlurb title='Tabbed navigation system'
        text="The tabbed navigation system allows users to cycle between new
        search queries and their shortlist of restaurants"/>
        <HeroImage imagesource={food_hero2}/>
        <Spacer />
        <CenteredBlurb title='Order or get directions directly from the restaurant page'/>
        <SizeableImage imagesource={food_hero3} width="80%"/>
        </Container>


        <Spacer />
        <Divider />
        <div id="Research" className="anchor"/>
        <CenteredBlurb title='Foodii is an app
        that helps you decide where to eat'
        smalltitle="Mobile app concept"
        text="Deciding where to go
        or what to order for a meal is an iterative and often messy process that involves balancing
        multiple variables. Foodii helps you easily browse restaurants using different criteria while
        also keeping track of a shortlist."/>
        <Divider />
        <CenteredBlurb title='User research' text="Development began
        with conducting user interviews to find out the different factors that
        people consider when trying to decide on a place to eat, and to identify the
        common pain points and challenges that they face in the process."/>
        <SizeableImage imagesource={food_interview} width="95%"/>
        <Divider />
        <CenteredBlurb title='Analyzing the findings' text="Through
        the user research, it was discovered that the most commonly recurring aspects
        that users considered included the location of a restaurant, its type of
        cuisine, its price, and reviews. Furthermore, a common frustration of the process was
        that on some occasions people simply don't have any specific criteria in mind,
        which prompted the inclusion of an 'explore' page in the app."/>
        <SizeableImage imagesource={defined} width="80%"/>
        <Spacer />
        <Divider />
        <div id="Diagramming" className="anchor"/>
        <div id="Mapping" className="anchor"/>
        <CenteredBlurb title="Mapping the process of 'choosing'"
        text="The next step was to diagram out the mental steps that people take when
        choosing a place to eat. This would serve as a guide when developing the
        page layout of the app."
        />
        <Spacer />
        <Container>

        <SizeableImage imagesource={choose} width="80%"/>
        </Container>
        <Spacer />
        <Divider />
        <CenteredBlurb title="Further analysis of the process of 'choosing'"
        text="It was discovered that the 'choosing process' is often
        made up of a repeating cycle of assessing a restaurant,
        editing a 'mental shortlist', refining search criteria, and making new searches.
        With this defined, the simplified diagram that's shown below was developed.
        "
        />
        <FullImage imagesource={functioned}/>
        <Spacer /><Spacer />
        <Divider />
        <div id="Personas" className="anchor"/>
        <div id="Architecture" className="anchor"/>
        <CenteredBlurb title="Developing the app architecture"
        text="Development of the app architecture began with defining user personas.
        These personas were iteratively sent through multiple drafts of the app layout
        in order to mature it into the final arrangement.
        "
        />
        <SizeableImage imagesource={personas} width="90%"/>
        <Spacer /><Spacer />
        <SizeableImage imagesource={journeycompiled} width=""/>
        <Spacer />
        <Divider />
        <CenteredBlurb title="Solidifying the page layout"
        text="From customer journey experimentation, it was decided that
        a tabbed navigation system would be used to support the cyclical
        'choosing' process. This would allow users to easily go back and forth
        between search methods without having to sort through multiple layers of
        pages and menus.
        "
        />
        <SizeableImage imagesource={page_architecture} />

        <Spacer />
        <Spacer />
        <Container>

        <Divider />
        <div id="Wireframing" className="anchor"/>
        <CenteredBlurb title="Wireframing"
        text=""
        />
        <SizeableImage imagesource={wireframes}/>
        <Spacer />
        <Divider />

        <CenteredBlurb title="Logo development"
        />
        <SizeableImage imagesource={fontcolor} width="80%"/>
        <Spacer />
        <Spacer />

        <Spacer/><Divider />
        <div id="Prototyping" className="anchor"/>
        <CenteredBlurb title="Prototyping"
        text="Digital prototypes were developed in order to conduct usability testing
        of the tabbed navigation system. 3 tabbed and 4
        tabbed versions were A/B tested to compare their effectiveness for browsing
        restaurants and modifying the shortlist.
        "
        />

        <SizeableImage imagesource={prototyping} width="100%"/>
        <div id="Testing" className="anchor"/>
        <CenteredBlurb title="User testing"
        text="User testing involved asking participants to walk through tasks
        in a simple prototype. Qualitative data such as user feedback was collected. Iterations of
        3 tabbed and 4 tabbed navigation systems were used. It was determined through this
        research that the 4 tabbed version where the shortlist was its own tab
        provided a smoother user experience.
        "
        />

        </Container>
        <Container>
        <HeroImage imagesource={finalscreens1} />


        <Spacer />

        </Container>
        <ScrollTop />
      </div>
    </div>
  );
}

export default FoodPage
