import React from 'react';
import Hero from '../components/Hero';
import GridTab from '../components/GridTab';
import ScrollTop from '../components/ScrollTop';
import Spacer from '../components/Spacer';
import Container from 'react-bootstrap/Container';
import LogoImage from '../components/LogoImage';
import FullImage from '../components/FullImage';
import ShortGrid from '../components/ShortGrid';
import ShorterGrid from '../components/ShorterGrid';
import SizeableImage from '../components/SizeableImage';
import CenteredBlurb from '../components/CenteredBlurb';

import animatedlogo4 from '../assets/images/AnimatedLogo4.gif';
import sans_highres3 from '../assets/images/sans_highres3.png';
import sans_name from '../assets/images/sans_name.png';

function WorkPage(props) {

  return (
    <div>
      <Spacer />

      <div className="d-flex justify-content-center workhomecontainer">
        <LogoImage imagesource={animatedlogo4} />
      </div>
      <Spacer />
      <div class="d-flex justify-content-center">
        <FullImage imagesource={sans_name}/>
      </div>
      <Container>
      <div class="d-flex justify-content-center">

        <h4 className="workheadertext"></h4>
      </div>
      <ShorterGrid />
      <Spacer />
      <Spacer />
      <ScrollTop />
      </Container>
    </div>
  );
}

export default WorkPage;
