import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu3 from '../components/Menu3';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import HeroLeftRight from '../components/HeroLeftRight';
import SizeableImage from '../components/SizeableImage';
import Divider from '../components/Divider';
import BackButton from '../components/BackButton';

import '../index.css';

import calendar_animation from '../assets/images/planchat/calendar_animation.gif';
import current_photoalbum from '../assets/images/planchat/current_photoalbum.png';
import edit_itinerary from '../assets/images/planchat/edit_itinerary.png';
import event_album from '../assets/images/planchat/event_album.png';
import event_animation from '../assets/images/planchat/event_animation.gif';
import itinerary_chat from '../assets/images/planchat/itinerary_chat.png';
import itinerary_timeline from '../assets/images/planchat/itinerary_timeline.png';
import new_event from '../assets/images/planchat/new_event.png';
import new_photoalbum from '../assets/images/planchat/new_photoalbum.png';
import past_events from '../assets/images/planchat/past_events.png';
import pcaccount_page from '../assets/images/planchat/pcaccount_page.png';
import pclogo_1 from '../assets/images/planchat/pclogo_1.png';
import pclogo_2 from '../assets/images/planchat/pclogo_2.png';
import pclogo_3 from '../assets/images/planchat/pclogo_3.png';
import pclogo_4 from '../assets/images/planchat/pclogo_4.png';
import planapp_architecture from '../assets/images/planchat/planapp_architecture.png';
import planchat_hero from '../assets/images/planchat/planchat_hero.png';
import planchat_wireframe from '../assets/images/planchat/planchat_wireframe.png';
import planchat_event_new from '../assets/images/planchat/event_animation_new.gif';
import pc_core from '../assets/images/planchat/pc_core.png';

function PlanchatPage(props) {

  return (
    <div>
        <BackButton2/>
        <div class="sidenav">
        <hr class="contentsdivider"/>
          <Menu3 />
          <hr class="contentsdivider"/>
        </div>
        <div class="sidemain">
          <div id="Summary" className="anchor"/>
          <Container>
          <HeroImage imagesource={planchat_hero}/>

          <CenteredBlurb title='PlanChat is an app that helps with organizing events'
          smalltitle="Mobile app concept"
          text="By integrating an event's Itinerary with a Chatroom, changes can be
          easily suggested to the group and voted on. Then, poll results automatically
          update the Itinerary to be easily viewed at
          any time."/>
          <Spacer />
          <SizeableImage imagesource={itinerary_chat} width="60%"/>
          <Spacer />
          <Divider />
          <CenteredBlurb title='View all of your upcoming events in a list or
          calendar layout' text="The Home Page shows all of your upcoming events
          and can be viewed in two different layouts,
          making sure you never miss plans that are coming up."/>

          <SizeableImage imagesource={calendar_animation} width="40%"/>




          </Container>

          <div id="Events" className="anchor"/>


          <Spacer />
          <Divider />
          <CenteredBlurb title="The Event Page"
          text="The Event Page is the hub that connects the Itinerary with
          the event's Chatroom. Upon opening an event, users see its most recent updates
           - chat notifications, pinned messages, and the most recent message.
          As users scroll down, they see the full Itinerary Timeline. Each Itinerary
          Item can be opened to view details on its location and attendees."
          />
          <Spacer />
          <Container>

          <SizeableImage imagesource={planchat_event_new} width="33%"/>
          </Container>
          <Spacer />
          <Divider />
          <CenteredBlurb title="The Itinerary Timeline"
          text="The Itinerary Timeline shows all of the event's activities
          as individual items. Often, people may not be able to attend the entire event.
          By dividing an event into seperate activities, it is easy for users
          to see exactly what is happening, when it is
          happening, and who is going."
          />

          <SizeableImage imagesource={itinerary_timeline} width="65%"/>
          <Spacer /><Spacer />
          <Divider />
          <CenteredBlurb title="Suggesting a change to an activity"
          text="" />

          <SizeableImage imagesource={edit_itinerary} width="95%"/>
          <Spacer /><Spacer />
          <Divider />
          <CenteredBlurb title="Creating a new event"
          text="Events are easily created by tapping the plus icon on the bottom
          menu bar."  />

          <SizeableImage imagesource={new_event} width="75%"/>
          <Divider />
          <CenteredBlurb title="Notifications are centralized in the Account Centre"
          text="Event invitations, chat notifications, and friend invitations
          are accessed in the Account Centre."  />

          <FullImage imagesource={pcaccount_page}/>


          <Divider />
          <div id="Research" className="anchor"/>
          <CenteredBlurb title="User interviews"
          text="This project began with conducting user interviews to identify common pain points
          when planning events. The focus of these interviews included the platforms that they commonly use, as well as any perceived limitations of these existing tools."
          />
          <Divider />
          <CenteredBlurb title="Key findings"
          text="Some of the most common platforms that were used included Discord and Facebook Events.
          In the former, a dedicated server is often created for a specific event, with some people also using
           pinned message to track updates. However, keeping track of changes to a plan remained a common source of confusion
           for many people. It was determined that this was due to the fact that pinned messages
           are not designed to contain detailed summaries in an easy-to-update format.
          "
          />
          <SizeableImage imagesource={pc_core} width="60%"/>
          <CenteredBlurb title=""
          text="From the user research, it was found that two interconnected tools are needed to effectively plan an event -
          a chatroom for communication, and an always up-to-date itinerary. This concept served as the core idea for the development of PlanChat.
          "
          />
          <Divider />
          <div id="Architecture" className="anchor"/>
          <CenteredBlurb title="App Architecture"
          text="The PlanChat app is centred around creating and managing Event Pages.
          Existing events are naturally to easy access as they are listed on the Home Page.
          However, it was also important for users to be able to create events from various
          pages of the app. This app allows users to create a brand new event from (1) the Home Page and
          (2) the Chat Page. Users can also duplicate an old event from a Past Event Page.
          "  />

          <SizeableImage imagesource={planapp_architecture} width="90%"/>
          <Spacer /><Spacer /><Spacer />
          <SizeableImage imagesource={planchat_wireframe} width="100%"/>
          <Spacer /><Spacer /><Spacer />
          <Divider />
          <Spacer />
          <CenteredBlurb title="The challenge of centralizing everbody's photos"
          text="Another issue that was identified with available tools was centralizing photos
          after an event. Currently, common solutions included a designated photographer creating
          a Google Drive of all the trip's photos, or attendees individually uploading photos
          to different locations and sharing download links. In PlanChat, all attendees can upload their photos to the Event Page
          for easy access."
          />
          <SizeableImage imagesource={current_photoalbum} width="70%"/>
          <Spacer/><Spacer/><Spacer/>

          <SizeableImage imagesource={new_photoalbum} width="50%"/>
          <Spacer /><Spacer /><Spacer />

          <SizeableImage imagesource={event_album} width="80%"/>

          <Container>
          <div id="Branding" className="anchor"/>



          </Container>
          <Container>

          <Spacer />



          <Spacer />
          </Container>
          <ScrollTop />

        </div>

    </div>
  );
}

export default PlanchatPage
